@charset "UTF-8";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html body {
  background: #121116;
  color: #ffffff;
  font-family: franklin-gothic-compressed, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px; }
  html body .wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
    grid-row-gap: 0;
    grid-template-areas: "hl hl hl hl hl hm hm hm hm hm hm hm" "c c c c c c c c c s s s" "fl fl fl fl ft fm fm fm fm fm fs fs";
    margin: 0 30px; }
  html body ul li {
    list-style-type: none; }

header .wrapper {
  grid-template-rows: 70px 0 0; }
  header .wrapper div {
    justify-self: start;
    align-self: center; }
  header .wrapper .logo {
    grid-area: hl; }
  header .wrapper .menu {
    grid-area: hm;
    display: flex;
    font-size: 20px;
    justify-self: end; }
    header .wrapper .menu li {
      display: flex;
      align-items: center;
      padding: 0 15px;
      cursor: pointer; }
      header .wrapper .menu li a {
        color: #ffffff;
        text-decoration: none; }
    header .wrapper .menu li:hover {
      background: #25242e; }
    header .wrapper .menu .search-box input {
      height: 35px;
      width: 235px;
      padding: 10px;
      background: #25242e;
      border: 0;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 5px;
      color: #ffffff;
      font-size: 16px; }
    header .wrapper .menu .search-box:hover {
      background: none; }
    header .wrapper .menu .active {
      background: #25242e;
      border-bottom: 4px red solid; }
      header .wrapper .menu .active a {
        margin-bottom: -4px; }

#slider .slide {
  height: 420px; }
  #slider .slide .blur {
    float: right;
    height: 100%;
    width: 60%;
    clip-path: polygon(15% 0, 100% 0%, 100% 100%, 0% 100%);
    background: rgba(18, 17, 22, 0.8);
    display: flex;
    justify-content: flex-end;
    align-items: center; }
    #slider .slide .blur .info {
      width: 70%;
      margin-right: 30px; }
      #slider .slide .blur .info .btn-more {
        cursor: pointer;
        padding: 18px 50px;
        border: 2px solid #fff;
        border-radius: 5px;
        display: inline-block; }
      #slider .slide .blur .info .btn-more:hover {
        background: #25242e; }
      #slider .slide .blur .info h2 {
        font-size: 28px;
        margin-bottom: 10px; }
      #slider .slide .blur .info p {
        font-size: 18px;
        margin-bottom: 20px; }

#content {
  margin: 35px 0 45px 0; }
  #content .bread {
    margin-bottom: 20px;
    font-size: 20px; }
    #content .bread hr {
      margin-top: 2px;
      border: none;
      color: red;
      background-color: red;
      height: 2px;
      width: 80px; }
  #content .wrapper #news {
    width: 100%;
    grid-area: c; }
    #content .wrapper #news .view-style {
      display: flex;
      justify-content: space-between; }
      #content .wrapper #news .view-style #grid-view, #content .wrapper #news .view-style #flow-view {
        cursor: pointer; }
    #content .wrapper #news .grid-feed {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      column-gap: 20px;
      grid-row-gap: 0; }
      #content .wrapper #news .grid-feed .news-preview {
        margin-bottom: 35px; }
        #content .wrapper #news .grid-feed .news-preview hr {
          margin: 6px 0;
          border: none;
          color: red;
          background-color: red;
          height: 2px;
          width: 80px; }
        #content .wrapper #news .grid-feed .news-preview img {
          width: 285px;
          margin-bottom: 10px; }
        #content .wrapper #news .grid-feed .news-preview .title {
          font-size: 20px; }
        #content .wrapper #news .grid-feed .news-preview .date {
          font-size: 13px;
          color: #787878; }
        #content .wrapper #news .grid-feed .news-preview .description {
          display: none; }
      #content .wrapper #news .grid-feed :nth-child(3n+1) {
        grid-column-start: 1;
        grid-column-end: 4; }
      #content .wrapper #news .grid-feed :nth-child(3n+2) {
        grid-column-start: 4;
        grid-column-end: 7; }
      #content .wrapper #news .grid-feed :nth-child(3n+3) {
        grid-column-start: 7;
        grid-column-end: 10; }
    #content .wrapper #news .flow-feed .news-preview {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      grid-template-areas: "pr pr pr dt dt dt dt dt dt" "pr pr pr t t t t t t" "pr pr pr d d d d d d";
      column-gap: 20px;
      grid-row-gap: 0;
      border-bottom: 1px solid rgba(84, 84, 84, 0.45);
      padding-bottom: 25px;
      margin-bottom: 25px; }
      #content .wrapper #news .flow-feed .news-preview hr {
        display: none; }
      #content .wrapper #news .flow-feed .news-preview img {
        grid-area: pr;
        width: 285px; }
      #content .wrapper #news .flow-feed .news-preview .title {
        grid-area: t;
        font-size: 24px; }
      #content .wrapper #news .flow-feed .news-preview .date {
        grid-area: dt;
        font-size: 18px;
        color: #787878; }
      #content .wrapper #news .flow-feed .news-preview .description {
        grid-area: d;
        font-size: 18px; }
    #content .wrapper #news .more {
      display: flex;
      justify-content: center; }
      #content .wrapper #news .more .btn-more {
        cursor: pointer;
        padding: 18px 24px;
        border: 2px solid #FFFFFF;
        border-radius: 5px;
        display: inline-block; }
        #content .wrapper #news .more .btn-more p {
          font-size: 18px; }
      #content .wrapper #news .more :hover {
        background: #25242e; }
  #content .wrapper #sidebar {
    grid-area: s; }
    #content .wrapper #sidebar hr {
      width: 70px; }
    #content .wrapper #sidebar .filter {
      margin-bottom: 40px; }
      #content .wrapper #sidebar .filter li {
        display: flex;
        justify-content: space-between;
        padding: 20px 27px;
        font-size: 18px;
        background: #25242e;
        border-bottom: 1px solid rgba(84, 84, 84, 0.45); }
        #content .wrapper #sidebar .filter li input {
          display: none;
          /* <--скрываем дефолтный чекбокс */ }
        #content .wrapper #sidebar .filter li span {
          /* <-- стилизируем новый */
          cursor: pointer;
          height: 15px;
          width: 15px;
          display: inline-block;
          position: relative;
          background: #DBDBDB;
          box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
          border-radius: 50%;
          padding: 3px; }
        #content .wrapper #sidebar .filter li [type=checkbox]:checked + span:before {
          /* <-- ставим иконку, когда чекбокс включен  */
          cursor: pointer;
          content: '•';
          position: absolute;
          top: -16px;
          left: 0;
          font-size: 43px;
          color: red; }

footer {
  background: #25242e; }
  footer a {
    color: #ffffff;
    text-decoration: none; }
  footer .wrapper {
    grid-template-rows: 0 0 125px; }
    footer .wrapper div {
      justify-self: start;
      align-self: center; }
    footer .wrapper .logo {
      grid-area: fl; }
    footer .wrapper .to-top {
      grid-area: ft; }
    footer .wrapper .sitemap {
      grid-area: fm;
      display: flex; }
      footer .wrapper .sitemap ul {
        margin-right: 60px; }
        footer .wrapper .sitemap ul li {
          line-height: 1.7em; }
    footer .wrapper .social {
      grid-area: fs;
      justify-self: end; }
      footer .wrapper .social ul li {
        display: inline; }
